body {
  background-color: #fefbf4;
}
.breadcrumb-item {
  a {
    text-decoration: none;
  }
}
.addToCart {
  width: 50%;
}
.product-origin {
  position: relative;
  color: #000;
  font-weight: bolder;
}
.product-origin::after {
  content: "";
  position: absolute;
  width: 100%;
  left: 0;
  height: 55%;
  border-bottom: 2px solid #bbb;
}
.product-detail {
  .product-detail-span {
    display: block;
    text-align: left;
  }
}
